import React from "react"
import ContentLoader from "react-content-loader"

const MyLoader = (props) => (
  <ContentLoader 
    speed={1.3}
    width={800}
    height={180}
    viewBox="0 0 800 180"
    backgroundColor="#28166f"
    foregroundColor="#00922b"
    {...props}
  >
    <rect x="250" y="38" rx="3" ry="3" width="500" height="20" /> 
    <circle cx="100" cy="90" r="80" /> 
    <rect x="250" y="77" rx="3" ry="3" width="500" height="20" /> 
    <rect x="250" y="113" rx="3" ry="3" width="500" height="20" />
  </ContentLoader>
)

export default MyLoader