import React from "react"
import ContentLoader from "react-content-loader"

const MyLoader2 = (props) => (
  <ContentLoader 
    speed={2}
    width={800}
    height={182}
    viewBox="0 0 800 182"
    backgroundColor="#00922b"
    foregroundColor="#28166f"
    {...props}
  >
    <rect x="16" y="4" rx="0" ry="0" width="237" height="300" /> 
    <rect x="281" y="4" rx="0" ry="0" width="237" height="300" /> 
    <rect x="544" y="3" rx="0" ry="0" width="237" height="300" />
  </ContentLoader>
)

export default MyLoader2